import React, { Component } from "react";
import * as routes from '../../../constants/routes';
import Axios from 'axios';
import * as Addresses from '../Addresses/';
import * as Contacts from '../ContactDetails';
import * as banking_routes from '../../../constants/banking-routes';
import * as address_routes from '../../../constants/addresses-routes';
import * as contacts_routes from '../../../constants/contacts-routes';
import * as policy_routes from '../../../constants/policy-routes';
import * as Utilities from '../../../constants/utilities';
import policy_holder_details from './Covers';

class CoverItem extends Component{
    constructor(props){
        super(props);
        this.state = { 
            cover: policy_holder_details
         };
    };


    componentWillMount(e){
        let cover = this.props.cover;
        this.setState({
            cover:cover
        });
    }

    render(){
        return(
            <tr>
                <td>{this.state.cover.surname}</td>
                <td>{this.state.cover.full_names}</td>
                <td>{this.state.cover.id_number}</td>
                <td>{this.state.cover.date_of_birth}</td>
                <td>{this.state.cover.nationality}</td>
            </tr>
        )
    }
}

class CoversList extends Component{
    constructor(props){
        super(props);
        this.state = { 
            covers_list : []
        };
    };


    componentWillMount(e){
        let covers_list = this.props.covers_list;
        this.setState({
            covers_list : covers_list
        });
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <table className={'table table-responsive'}>
                    <thead>
                        <tr>
                            <td><strong>Surname</strong></td>
                            <td><strong>Names</strong></td>
                            <td><strong>ID</strong></td>
                            <td><strong>DOB</strong></td>
                            <td><strong>Nationality</strong></td>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.covers_list.map(cover => { 
                                return(
                                    <CoverItem
                                        cover={cover}
                                    />
                                )
                             })
                        }

                    </tbody>
                    <tfoot>
                        <tr>
                            <td><strong>Surname</strong></td>
                            <td><strong>Names</strong></td>
                            <td><strong>ID</strong></td>
                            <td><strong>DOB</strong></td>
                            <td><strong>Nationality</strong></td>
                        </tr>
                    </tfoot>
                </table>
            </div>
        )
    }
}


class BrowseCovers extends Component{
    constructor(){
        super();
        this.state = { 
            covers_list : [],
            show_covers : false
        };

        this.load_covers = this.load_covers.bind(this);
        this.onShow = this.onShow.bind(this);
    };

    load_covers(e){
        let self = this;    
    }

    onShow(e){

        switch(e.target.name){
            case 'browse-covers':
                this.setState({
                    show_covers: true
                });break;
            default:break;
        }

    }

    
    componentWillMount(e) {
        
    }
    

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}><strong> <i className='fa fa-folder-open'> </i> Browse Covers </strong></h3>

                    <div className={'row pull-right'}>
                        <button className='btn btn-box-tool btn-outline-dark' name={'browse-covers'} onClick={e => this.onShow(e)}>
                            <strong> <i className='fa fa-users'> </i> Covers </strong>
                        </button>

                    </div>


                </div>

                {
                    (this.state.show_covers) ?
                        <CoversList
                         covers_list ={this.state.covers_list}
                        />
                    :''
                }


            </div>

        )
    }
}


export default BrowseCovers;